<template>
  <div class="js_wrapper" v-loading="loading">
    <el-collapse v-model="activeNames" @change="handleChange">
        <el-collapse-item v-for="item in jsInfors"  v-bind:key="item.id" v-bind:title="item.question" v-bind:name="item.id">
            <div class="content" v-html="item.answer"></div>
        </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
import { ERR_OK } from "@/api/config.js";
export default {
  data() {
    return {
      activeNames: [0],
      jsInfors: null,
      activeEl: 0,
      loading: true
    };
  },
  created() {
    this._getJsInfor();
  },
  computed: {
  },
  methods: {
    handleChange(val) {
        console.log(val);
    },
    _getJsInfor() {
      this.$http("get","https://www.easy-mock.com/mock/5b3212320a29144fd436ee1e/baseInfor/js"
      ).then( res => {
          if (res.status == ERR_OK) {
            console.log(res);
            this.jsInfors = res.data.data
            this.loading = false
          }
        }, err => {
          console.log(err.message);
        }
      );
    }
  }
};
</script>
<style scoped>
.js_wrapper{
  width: 100%;
  height: 100%;
}
.content{
    text-align: left;
    padding-left: 20px;
    padding-right: 20px;
    line-height: 25px;
}
</style>




